import {splitQuery} from "./lib/utils";
import {query} from "./lib/query";


export default function Fillo(dataFile) {
    let db;
    this.init = function() {
        importExceltoJson(dataFile, (data) => {
            console.log(data);
            db = data.db;
        });
    }
    this.query = function(str, params) {
        return query(db, str, params);
    }
}