var sqlite3 = require('sqlite3').verbose();
var db = new sqlite3.Database(':memory:');

export function connectDB (data, cb) {    
    db.serialize(function() {
        const columns = Object.keys(data[0]);
        const columnString = columns.map(key => `${key} TEXT`).join();
        db.run(`CREATE TABLE test (${columnString})`);
        console.log("DB created");
        console.log(data[0]);
        const params = Array(columns.length).fill('?').join();
        console.log(params);
        var stmt = db.prepare(`INSERT INTO test VALUES (${params})`);
        data.map(item => {
            stmt.run(item.id, item.name);
        });console.log("1DB created");
        stmt.finalize();
        console.log("2DB created");
        db.each("SELECT * FROM test", function(err, row) {
            console.log(row);
        }, () => {
            cb(null, {
                message: "Table created from excel file",
                db
            });
        });
    });
 
}

export function closeDB () {
    db.close();
}